@import url(https://fonts.googleapis.com/css2?family=Cinzel+Decorative&display=swap);
.dark {
  --bg-0: #000000;
  --bg-1: #111111;
  --bg-2: #222222;
  --bg-3: #333333;

  --text-100: #ffffff;
  --text-50: #777777;

  --accent-100: #448866;
  --accent-50: #224433;
}

*:focus {
  outline: none;
}

html,
body,
#root {
  height: 100vh;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.cipherbox-container {
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
}

.cipherlist-container {
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
}

.app {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.app.dark {
  background-color: var(--bg-0);
  color: var(--text-100);
  align-items: center;
}

.btn-add {
  background-color: var(--bg-1);
  color: var(--text-50);
  border: solid 2px var(--bg-2);
  padding: 0.5rem;
  border-radius: 3px;
  transition: all 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);
}

.btn-add:hover,
.btn-add:focus {
  color: var(--text-100);
  border-color: var(--text-100);
}

.btn-img {
  border: none;
  opacity: 0.5;
  background-color: transparent;
  padding: 0.5rem;
  transition: opacity 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);
}

.btn-img:hover,
.btn-img:focus {
  opacity: 1;
}

.cipherbox {
  display: flex;
  flex-direction: column;
  border: var(--bg-2) solid 2px;
  flex-grow: 1;
  width: 300px;
  height: 200px;
  border-radius: 3px;
  background-color: var(--bg-1);
  transition: border-color 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);
}

.cipherbox:focus {
  border-color: var(--text-100);
}

.cipherbox-titlebar {
  display: flex;
  align-items: center;
}

.cipherbox-select option {
  background: transparent;
}

.cipherbox-handle {
  flex-grow: 1;
}

.cipherbox-content {
  display: block;
  padding: 1rem;
  resize: none;
  border: none;
  margin: 0;
  flex-grow: 1;
  background-color: transparent;
  color: var(--text-50);
  transition: color 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);
}

.cipherbox-content:focus {
  color: var(--text-100);
  font-family: "Raleway", sans-serif;
}

.cipherbox-select {
  background: transparent;
  border: none;
  color: var(--text-50);
  margin: 0;
  padding-inline: 1rem;
  transition: color 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);
}

.cipherbox-select:focus,
.cipherbox-select:hover {
  color: var(--text-100);
}

.cipherbox-select option {
  background-color: var(--bg-1);
}

.input {
  display: inline-flex;
  flex-direction: column;
  color: var(--text-50);
}

.input:focus-within,
.input-field:hover {
  color: var(--text-100);
}

.input-title {
  font-size: 13.333px;
  -webkit-padding-start: 0.5rem;
          padding-inline-start: 0.5rem;
  transition: color 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);
}

.input-field {
  color: currentColor;
  background-color: transparent;
  border: none;
  padding: 0.5rem;
  transition: color 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);
}

.cipherbox-settings {
  padding: 0.5rem;
}

.app-footer {
  padding: 1rem;
  color: var(--text-50);
}

.app-title {
  font-family: "Cinzel Decorative", sans-serif;
  font-weight: normal;
  height: 4rem;
  display: flex;
}

.app-title-letter {
  display: inline-block;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 2rem;
  width: 2rem;
}

